<script>
import Home from './views/Home'

export default {
  name: 'App',

  components: {
    Home
  },

  data: () => ({
    padless: false
  }),
  computed: {
    localAttrs () {
      const attrs = {}

      if (this.variant === 'default') {
        attrs.absolute = false
        attrs.fixed = false
      } else {
        attrs[this.variant] = true
      }
      return attrs
    }
  }
}
</script>
